<template>
	<section>
		<div class="inr-banner">
			<img :src="this.imgUrlBanner" alt=""/>
		</div>
		<div class="inner">
      <div class="inf-info inf-sxtj">当前位置：<a href="">首页 &gt;</a><span class="inf-info-tet"> 实货贴水</span></div>
       <div class="oliTy">
	    <p class="clearfix olia"><span class="youName">油种</span><input type="text" class="you" placeholder="请输入油种" v-model="oilType" @keyup.delete="show3()"></p>	
		<p class="haha">
			<span class="demonstration">价格日期</span>
			<el-date-picker 
			   value-format="yyyy-MM-dd"
		  	  format="yyyy-MM-dd"
		      v-model="timeRange"
		      type="daterange"
		      range-separator="至"
		      start-placeholder="开始日期"
		      size="small"
		      class="w200"
		      end-placeholder="结束日期">
		    </el-date-picker>
		</p>  
	     <button class="btnOli" type="button" @click="searchFn()">搜索</button>
	  </div>
      <div class="inr-que">
      	<a href="javascript:;" class="inr-ckzst" @click="trendFn">查看走势图</a>
       <table>
        <tr class="inrq-t">
          <th>原油名称</th>
          <th>计价基准</th>
          <th>价格日期</th>
          <th>装货月</th>
          <th>贴水</th>
          <th>变化</th>
          <th>运输条款</th>
          <!--<th>效益（美元/吨）</th>-->
          <th>备注</th>
        </tr>
        <tr class="inf-tab"  v-for="(item,index) in dataList">            
          <td>{{item.extend2}}</td>
          <td>{{item.extend3}}</td>
          <td>{{item.pricingDate|timestampFormat('YYYY-MM-DD')}}</td>
          <td>{{item.loadingMonth}}</td>
          <td>{{item.pricing}}</td>
          <td>{{item.change1}}</td>
          <td>{{item.transportClause}}</td>
         <!-- <td>{{item.extend5}}</td>-->
          <td>{{item.extend4}}</td>
        </tr>
       </table> 
      </div>
    <div style="text-align: center; margin-top: 30px;" v-if="dataList.length != 0">
   		<el-pagination
   			 :page-size="pageSize"
	     @current-change='pageFn'
		 layout="prev, pager, next"
		 :total="total">
		</el-pagination>
	</div> 
    </div>
     <el-dialog
		title="尊敬的用户:"
		:visible.sync="dialogVisible"
		width="30%"
		:before-close="handleClose">
		<span>您查看的数据是付费内容，请订阅浏览</span>
		<span slot="footer" class="dialog-footer">
			<el-button @click="dialogVisible = false">不,继续免费试试</el-button>
			<el-button type="info" disabled>￥<span>498</span>/年 </el-button>
		</span>
	</el-dialog>
	</section>
</template>

<script>


	import {getAgioT,queryCarousels} from '../../api/info';
	export default {
		data() {
			return {
				 dialogVisible:true,
				imgUrlBanner:'',
				dataList:[],
				oilType:'',
				timeRange:'',
				total:0,
				pageNum:1,
				pageSize:20
			}
		},
		methods: {
			getData:function(){
				var param = {
					'crudeNameE':'',
		    		'startDate':'',
		    		'pageNum' :this.pageNum,
		    		'pageSize': this.pageSize
				}
		        getAgioT(param).then((res)=>{
		        	if(res.status == 9999) {
						window.location.href=configURL.sys+"/login.htm";
						//跳登入页面
						return false;
					}
					this.dataList=res.datas;
					this.total = res.total
				})
	     	},
	     	trendFn(){
	     		this.$router.push({path:'/agioTrend'})
	     	},
          	pageFn(sty){
          		this.pageNum=sty
          		var param = {
					'crudeNameE':this.oilType,
		    		'startDate':this.timeRange[0] || '',
		    		'endDate':this.timeRange[1] || '',
		    		'pageNum' :sty,
		    		'pageSize': this.pageSize
				}
		        getAgioT(param).then((res)=>{
					this.dataList=res.datas;
					this.total = res.total
				})
          	},
          	searchFn(){
          		
          		if(this.timeRange){
          			var param = {
						'crudeNameE':this.oilType,
			    		'startDate':this.timeRange[0],
			    		'endDate':this.timeRange[1],
			    		'pageNum' :this.pageNum,
			    		'pageSize': this.pageSize
					}
          		}else{
          			var param = {
						'crudeNameE':this.oilType,
			    		'startDate':'',
			    		'endDate':'',
			    		'pageNum' :this.pageNum,
			    		'pageSize': this.pageSize
					}
          		}
          		
		        getAgioT(param).then((res)=>{
					this.dataList=res.datas;
					this.total = res.total
				})
          	},
          	getBanner(){
				queryCarousels({typeCode:"WEB",pageCode:"zx_index_barner",pageNum:1,pageSize:10}).then((res)=>{
					
					this.imgUrlBanner = res.datas[0].imageUrl
					
				});
			},
			show3(){
				var param = {
					'crudeNameE':this.oilType,
		    		'startDate':this.timeRange[0],
		    		'endDate':this.timeRange[1],
		    		'pageNum' :this.pageNum,
		    		'pageSize': this.pageSize
				}
		        getAgioT(param).then((res)=>{
					this.dataList=res.datas;
					this.total = res.total
				})
			}
		},
		mounted(){
			
			this.getData();
			this.getBanner()
    }
	}

</script>
<style>
.w350{width:350px!important;}
.childTable{width:100%;}
.childTable tr:last-child{border-bottom: 0px;}
.youName{font-size:14px;color:#2c3e50; margin-left: 20px; float: left;line-height: 34px; display: block; margin-right: 20px;}
.w350{width:350px!important;}
.childTable{width:100%;}
.childTable tr:last-child{border-bottom: 0px;}
.you{ float:left;width:300px;height:38px;background:#fff;line-height:34px;border:0; border-radius:4px;padding-left:20px;font-size:14px;color:#2c3e50;border: 1px solid #C0C0C0;}
.youName{font-size:14px;color:#2c3e50; margin-left: 20px; float: left;line-height: 34px; display: block; margin-right: 20px;}
.oliTy{background: #fff; padding: 20px 0;}
.oliTy:after{content: '';height: 0px;clear: both;display: block;}
.haha{float:left; margin-right:60px ;position: relative !important;}
.olia{float: left;margin-right: 50px;}
.demonstration{margin-right: 20px;}  
.el-input__inner{height: 36px !important;border: 1px solid #C0C0C0;}
.btnOli{float:left;width:10%;height:40px;background:#66a6fe;border:0;border-radius:4px;color:#fff;font-size:18px;} 
.demonstration{font-size:14px;color:#2c3e50;}
.el-month-table td.current{
	position:relative; 
     top:inherit !important; 
     left:inherit !important; 
     width:auto !important; 
     height:auto !important; 
     border:none !important; 
     box-shadow:none !important; 
     border-radius:inherit !important; 
}     
 .inr-ckzst{
 	top: -136px !important;
 } 
  .fiexWidth{width:76px !important;} 
</style>


